import Vue from 'vue'
import VueRouter from 'vue-router'
import bird from '../views/bird/index.vue'

Vue.use(VueRouter)

const routes = [
    {
    path: '/',
    redirect: "/bird"
  },
  {
    path: '/bird',
    name: 'bird',
    component: bird
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
